import React from "react";
import Carousel from "react-material-ui-carousel";
import { Box, Typography } from "@mui/material";
import sq1 from "../images/ex1.jpg";
import sq2 from "../images/cat.jpg";
import ex1 from "../images/earth.PNG";
import ex2 from "../images/fire.PNG";
import { styled } from "@mui/system";

const Root = styled(Box)(({ theme, img }) => ({
  justifyContent: "center",
  alignItems: "center",
  display: "flex",
  width: "100%",
  backgroundImage: `url(${img})`,
  backgroundSize: "cover",
  backgroundPosition: "center",
  color: "#fff",

  [theme.breakpoints.down("sm")]: {
    //backgroundColor: red[500],
    //maxWidth: 240,
    //minWidth: 240,
    height: 220,
  },
  [theme.breakpoints.up("sm")]: {
    //backgroundColor: blue[500],
    height: 340,
  },
  [theme.breakpoints.up("lg")]: {
    //backgroundColor: green[500],
    height: 420,
  },
}));

const Root2 = styled(Typography)(({ theme }) => ({
  component: "div",
  variant: "h1",
  [theme.breakpoints.down("sm")]: {},
  [theme.breakpoints.up("sm")]: {},
  [theme.breakpoints.up("lg")]: {},
}));

function ca(props) {
  var items = [
    {
      name: "",
      description:
        "~~~~",
      img: sq1,
    },
    {
      name: "   ",
      description:
        "...  ...     ....         ...      ....  ...    ....    .....  ...            ...^^ ...   .........  ...    ..... .....     .....",
      img: sq2,
    },
  ];

  return (
    <Carousel>
      {items.map((item, i) => (
        <Item key={i} item={item} />
      ))}
    </Carousel>
  );
}

function Item(props) {
  const img = props.item.img;
  return (
    <Root img={img}>
      <Box
        justifyContent="center"
        alignItems="center"
        sx={{
          width: 300,
          height: 300,
          overflow: "hidden",
          ml: { xs: 42, md: 80 },
          mr: { xs: 4, md: 0 },
        }}
      >
        <Box
          sx={{
            mt: { xs: 5, md: 2 },
            justifyContent: "center",
            alignItems: "center",
            fontSize: { xs: 20, md: 30 },
            fontWeight: "bold",
            maxHeight: 42,
            textOverflow: "ellipsis",
            overflow: "hidden",
            color: "yellow,"
          }}
        >
          {props.item.name}
        </Box>
        <Box
          sx={{
            fontSize: { xs: 14, md: 15 },
            fontWeight: "bold",
            mt: { xs: 3, md: 6 },
            maxHeight: 42,
            //color: "yellow,"
          }}
        >
          {props.item.description}
        </Box>
      </Box>
    </Root>
  );
}

export default ca;
