import React from 'react';
import myStyle from './MainPage.module.scss';
import { Grid, Box, Container } from '@mui/material';
import ResponsiveAppBar from '../../component/ResponsiveAppBar';
import Carousel from '../../component/Carousel';
import Carousel2 from "../../component/Carousel2";
import Carousel3 from "../../component/Carousel3";
import SearchBar from '../../component/SearchBar';
import SearchBar2 from '../../component/SearchBar2';
import { Categorie } from '../../component/Categorie';
import Cards from '../../component/Cards';
import Test01 from "../../component/test01";
import Test02 from "../../component/test02";


const MainPage = () => {



  return (
    <div className="main">
      <Container
        sx={{
          minWidth: { xs: "100%", md: "1200px" },
          maxWidth: { xs: "100%", md: "2560px" },
        }}
        disableGutters={true}
        container
        justifyContent="center"
      >
        <Grid container justifyContent="center">
          <ResponsiveAppBar />
        </Grid>
        <Carousel3 />
        <Grid container justifyContent="center">
          <SearchBar2 />
          <Categorie />
        </Grid>
        <Cards />
        <Cards />
        <Cards />
        <Box
          justifyContent="center"
          alignItems="center"
          sx={{
            mt: "200px",
            width: "100",
            height: "300px",
            backgroundColor: "gray",
          }}
        ></Box>
      </Container>
    </div>
  );
};

export default MainPage;
