import * as React from "react";
import { styled } from "@mui/material/styles";
import Avatar from "@mui/material/Avatar";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
import { blue, green, red, gray, amber } from "@mui/material/colors";
import FavoriteIcon from "@mui/icons-material/Favorite";
import ShareIcon from "@mui/icons-material/Share";
import { Paper } from "@mui/material";
import { Box } from "@mui/system";
import sq2 from "../images/cat.jpg";
import { ThemeProvider, createTheme } from "@mui/system";
import Grid from "@mui/material/Grid";

import LinearProgress, {
  linearProgressClasses,
} from "@mui/material/LinearProgress";

const BorderLinearProgress = styled(LinearProgress)(({ theme }) => ({
  height: 8,
  borderRadius: 4,
  [`&.${linearProgressClasses.colorPrimary}`]: {
    backgroundColor:
      theme.palette.grey[theme.palette.mode === "light" ? 200 : 800],
  },
  [`& .${linearProgressClasses.bar}`]: {
    borderRadius: 4,
    backgroundColor: theme.palette.mode === "light" ? "#f48fb1" : "#308fe8",
  },
}));

const Root = styled(Paper)(({ theme }) => ({
  //padding: 1,
  //backgroundColor: amber[50],
  [theme.breakpoints.down("md")]: {
    //backgroundColor: red[500],
    maxWidth: "45%",
    maxHeight: 210,
    minWidth: "45%",
    minHeight: 210,

    margin: 5,
  },
  [theme.breakpoints.up("md")]: {
    //backgroundColor: blue[500],
    maxWidth: 260,
    maxHeight: 210,
    minWidth: 260,
    minHeight: 210,

    margin: 5,
  },
  [theme.breakpoints.up("lg")]: {
    //backgroundColor: green[500],
    margin: 10,
    maxWidth: 300,
    maxHeight: 210,
    minWidth: 300,
    minHeight: 210,
  },
}));

const theme1 = createTheme({
  palette: {
    background: {
      paper: "#fff",
    },
    text: {
      primary: "#173A5E",
      secondary: "#46505A",
    },
    action: {
      active: "#001E3C",
    },
    success: {
      dark: "#009688",
    },
  },
});

const Card2 = () => {
  return (
    //<ThemeProvider theme={theme1}>
    <Root elevation={0}>
      <Box
        height={120}
        //sx={{ display: "flex" }}
        style={{
          //backgroundImage: `url(${sq2})`,
          backgroundImage: `url("https://mud-kage.kakaocdn.net/dn/TSBSF/btrubLNogFE/jcfHPjZskQgfLgxKEwz4aK/c360.jpg")`,
          backgroundSize: "cover",
          backgroundPosition: "center",
          borderRadius: "6px",
        }}
      />
      <Box
        component="div"
        sx={{
          fontSize: 14,
          fontWeight: "bold",
          marginTop: 0.4,
          textOverflow: "ellipsis",
          maxHeight: 42,
          overflow: "hidden",
          //color: "yellow,"
        }}
      >
        "''  ''    !"
      </Box>
      <Grid container spacing={1}>
        <Grid item xs={8}>
          <Box
            sx={{
              display: "inline",
              fontWeight: "bold",
              mx: 0.5,
              fontSize: 12,
            }}
          >
             
          </Box>
        </Grid>
        <Grid item xs={4} sx={{ direction: "rtl" }}>
          <IconButton aria-label="add to favorites">
            <FavoriteIcon
              sx={{
                fontSize: 14,
              }}
            />
          </IconButton>
          <IconButton aria-label="share">
            <ShareIcon
              sx={{
                fontSize: 14,
              }}
            />
          </IconButton>
        </Grid>
      </Grid>

      <BorderLinearProgress variant="determinate" value={50} />
    </Root>
    //</ThemeProvider>
  );
};

export default Card2;
